*,
::after,
::before {
    box-sizing: border-box;
}

/* fonts */
html {
    font-size: 100%;
    overflow-y: overlay;
}

/* variables */
:root {
    /* primary */
    --primary-100: #e9e2e7;
    --primary-200: #d3c5cf;
    --primary-300: #bda8b8;
    --primary-400: #a78ba0;
    --primary-500: #916e88; /* hsl(315.4,13.7%,50%) */
    --primary-600: #74586d;
    --primary-700: #574252;
    --primary-800: #3a2c36;
    --primary-900: #1d161b;

    /* secondary */
    --secondary-100: #e2e9e4;
    --secondary-200: #c5d3c9;
    --secondary-300: #a8bdad;
    --secondary-400: #8ba792;
    --secondary-500: #6e9177; /* hsl(135.4,13.7%,50%) */
    --secondary-600: #58745f;
    --secondary-700: #425747;
    --secondary-800: #2c3a30;
    --secondary-900: #161d18;

    /* grey */
    --grey-50: #f8fafc;
    --grey-100: #f1f5f9;
    --grey-200: #e2e8f0;
    --grey-300: #cbd5e1;
    --grey-400: #94a3b8;
    --grey-500: #64748b;
    --grey-600: #475569;
    --grey-700: #334155;
    --grey-800: #1e293b;
    --grey-900: #0f172a;

    /* colors */
    --black: #222;
    --white: #fff;
    --red-light: #f8d7da;
    --red-dark: #a70000;
    --green-light: #d1e7dd;
    --green-dark: #0f5132;
    --gold: #ffc000;

    /* fonts  */
    --headingFont: 'Lato', sans-serif;
    --bodyFont: 'Montserrat', sans-serif;
    --smallText: 0.7em;

    /* vars */
    --backgroundColor: var(--grey-50);
    --textColor: var(--grey-900);
    --borderRadius: 0.25rem;
    --letterSpacing: 1px;
    --transition: 0.3s ease-in-out all;
    --max-width: 1120px;
    --fixed-width: 600px;
    --overlay: rgba(0, 0, 0, 0.4);
	--navbar-height: 5rem;
	--footer-height: 4rem;
	--page-width: 85vw;
	--mobile-width: 95vw;

    /* box shadow*/
    --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
    --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
    --shadow-5: 0 0 8px 0 rgba(0, 0, 0, 0.05),
    0 2px 8px 0 rgba(0, 0, 0, 0.08);
    --shadow-6: inset 0 -3px 0 0 var(--primary-400);
}

body {
    background: var(--backgroundColor);
    font-family: var(--bodyFont);
    font-weight: 300;
    line-height: 1.75;
    color: var(--textColor);
}

p {
    margin-top: 0;
    margin-bottom: 1.5rem;
}

h1,
h2,
h3,
h4,
h5 {
    margin: 0;
    margin-bottom: 1.38rem;
    font-family: var(--headingFont);
    font-weight: 400;
    line-height: 1.3;
    text-transform: capitalize;
    letter-spacing: var(--letterSpacing);
}

h1 {
    margin-top: 0;
    font-size: 3.052rem;
}

h2 {
    font-size: 2.441rem;
}

h3 {
    font-size: 1.953rem;
}

h4 {
    font-size: 1.563rem;
}

h5 {
    font-size: 1.25rem;
}

small,
.text-small {
    font-size: var(--smallText);
}

a {
    text-decoration: none;
}
ul {
    list-style-type: none;
    padding: 0;
}

.img {
    width: 100%;
    display: block;
    object-fit: cover;
}

/* buttons */
.btn {
    cursor: pointer;
    color: var(--white);
    background: var(--primary-500);
    border: transparent;
    border-radius: var(--borderRadius);
    letter-spacing: var(--letterSpacing);
    padding: 0.375rem 0.75rem;
    box-shadow: var(--shadow-1);
    transition: var(--transtion);
    text-transform: capitalize;
    display: inline-block;
}
.btn:hover {
    background: var(--primary-700);
    box-shadow: var(--shadow-3);
}
.btn-hipster {
    color: var(--primary-500);
    background: var(--primary-200);
}
.btn-hipster:hover {
    color: var(--primary-200);
    background: var(--primary-700);
}
.btn-block {
    width: 100%;
}

/* alerts */
.alert {
    padding: 0.375rem 0.75rem;
    margin-bottom: 1rem;
    border-color: transparent;
    border-radius: var(--borderRadius);
}

.alert-danger {
    color: var(--red-dark);
    background: var(--red-light);
}
.alert-success {
    color: var(--green-dark);
    background: var(--green-light);
}

/* form */
.form {
    width: 90vw;
    max-width: var(--fixed-width);
    background: var(--white);
    border-radius: var(--borderRadius);
    box-shadow: var(--shadow-2);
    padding: 2rem 2.5rem;
    margin: 3rem auto;
}
.form-label {
    display: block;
    font-size: var(--smallText);
    margin-bottom: 0.5rem;
    text-transform: capitalize;
    letter-spacing: var(--letterSpacing);
}
.form-input,
.form-textarea {
    width: 100%;
    padding: 0.375rem 0.75rem;
    border-radius: var(--borderRadius);
    background: var(--backgroundColor);
    border: 1px solid var(--grey-200);
}

.form-row {
    margin-bottom: 1rem;
}

.form-textarea {
    height: 7rem;
}
::placeholder {
    font-family: inherit;
    color: var(--grey-400);
}
.form-alert {
    color: var(--red-dark);
    letter-spacing: var(--letterSpacing);
    text-transform: capitalize;
}

/* alert */
@keyframes spinner {
    to {
        transform: rotate(360deg);
    }
}

.loading {
    width: 6rem;
    height: 6rem;
    border: 5px solid var(--grey-400);
    border-radius: 50%;
    border-top-color: var(--primary-500);
    animation: spinner 0.6s linear infinite;
}
.loading {
    margin: 0 auto;
}
/* title */

.title {
    text-align: center;
}

.title-underline {
    background: var(--primary-500);
    width: 7rem;
    height: 0.25rem;
    margin: 0 auto;
    margin-top: -1rem;
}

/*
=============== 
Navbar
===============
*/

#navbar {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-500);
    z-index: 99999;
    height: var(--navbar-height);
}

.nav-center {
    width: var(--page-width);
    display: flex;
    align-items: center;
}

.nav-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 2rem;
}

.nav-logo {
    display: flex;
    align-items: center;
}

.nav-header img {
    margin-right: 0.7rem;
    height: 3rem;
    user-select: none;
}

.header-name {
    color: var(--white);
    font-family: 'Roboto';
    font-weight: 300;
    font-size: 1.2rem;
    margin: 0;
    height: 3rem;
    white-space: nowrap;
    user-select: none;
}
.header-name span {
    font-weight: 700;
} 

.nav-btn i {
    font-size: 1.25rem;
    padding-top: 1px;
    padding-left: 1.5px;
}

#nav-links {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: var(--navbar-height);
}

.nav-btn {
    display: none;
}

.nav-link {
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 1rem;
    letter-spacing: var(--letterSpacing);
    margin-left: 2rem;
    padding: 0 0.5rem;
    transition: var(--transition);
    border-top: 3px solid transparent;
    border-bottom: 3px solid transparent;
    user-select: none;
}
/* TODO: merge FireFox:has() */
.nav-link:hover{
    border-bottom: 3px solid var(--white);
}
.nav-link:has(a.active) {
    border-bottom: 3px solid var(--white);
}

.nav-link span img {
    width: 10px;
    margin-left: 7px;
    margin-bottom: 2px;
}

.nav-link a {
    color: var(--black);
}
.nav-link:hover > a,
.nav-link a.active {
    color: var(--white);
}

.nav-link > a,
.nav-link > span {
    color: var(--black);
    display: block;
    height: 100%;
    line-height: var(--navbar-height);
}

.sub-menus {
    display: none;
    position: absolute;
    background-color: var(--grey-200);
    top: var(--navbar-height);
    padding: 1rem;
}
.nav-link:hover .sub-menus {
    display: flex;
}

.sub-menu-title {
    font-size: 0.85rem;
    margin-bottom: 0.9rem;
    text-align: left;
}

.sub-menu {
    margin-right: 30px;
}
.sub-menu:last-of-type {
    margin-right: 0;
}

.sub-menu li {
    text-align: left;
    font-size: 0.8rem;
}
.sub-menu a:hover,
.sub-menu a.active {
    color: var(--primary-600);
}

/* TODO: merge FireFox:has() */
.join-btn {
    padding: 0.2rem 1rem;
    background-color: var(--primary-700);
    height: auto;
    border-bottom: 5px solid var(--primary-800);
    transition: none;
}
.join-btn:has(a.active) {
    padding: 0.2rem 1rem;
    background-color: var(--primary-700);
    height: auto;
    border-bottom: 5px solid var(--primary-800);
    transition: none;
}
.join-btn:hover {
    border-bottom: none !important;
}

.join-btn a {
    display: unset;
    line-height: unset;
    color: var(--white) !important;
}

/* TODO: change width */
@media screen and (max-width: 1800px) {
    .nav-center {
        width: var(--mobile-width);
    }

    .nav-btn {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 3rem;
        width: 100%;
    }
    .nav-btn button {
        height: 100%;
        aspect-ratio: 1;
        background-color: var(--primary-500);
        border: none;
        cursor: pointer;
    }
    .nav-btn button:focus {
        outline: none;
    }

    .nav-btn img {
        width: 100%;
        max-width: none;
    }

    #nav-links {
        display: block;
        transform: translateX(-100vw);
        height: auto;
        position: absolute;
        top: var(--navbar-height);
        left: 0;
        background-color: var(--white);
    }
    #nav-links.show {
        transform: none;
    }

    .nav-link {
        display: flex;
        flex-direction: column;
        padding: 0;
        width: 100%;
        margin: 0;
    }
    .nav-link > span:hover,
    .nav-link:hover > a,
    .nav-link a.active {
        color: var(--primary-600);
    }

    .nav-link > a,
    .nav-link > span {
        line-height: unset;
        height: 100%;
        width: 100%;
        margin: 0.6rem 0;
    }

    .nav-link span {
        cursor: pointer;
    }

    .sub-menus {
        top: unset;
        background-color: unset;
        padding: 1rem 1rem 0;
        width: 100%;
        position: relative;
    }
    .nav-link:hover .sub-menus {
        display: unset;
    }
    .nav-link:hover .sub-menus:not(.active) {
        display: none;
    }
    .sub-menus.active {
        display: block;
    }

    .sub-menu {
        margin-bottom: 2rem;
    }
    .sub-menu:last-of-type {
        margin-bottom: 0;
    }

    .sub-menu li a {
        display: block;
    }

    .join-btn {
        margin: 0;
        border: none !important;
        transform: var(--transition);
    }
    .join-btn:hover {
        background-color: var(--primary-600) !important;
    }

    .join-btn a {
        padding: 0.8rem 0 !important;
        height: 100%;
        width: 100%;
        margin: unset;
    }
}

/*
=============== 
Page
===============
*/

.page {
    min-height: calc(100vh - (var(--navbar-height) + var(--footer-height)));
    padding-bottom: 2rem;
}

section {
    width: var(--page-width);
    margin: 0 auto;
    padding: 1.5rem 0;
}

@media screen and (max-width: 1250px) {
    section {
        width: var(--mobile-width);
    }
}

/*
=============== 
Footer
===============
*/

.page-footer {
    height: var(--footer-height);
    text-align: center;
    background: var(--black);
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
}

.page-footer p {
    margin-bottom: 0;
}

/*
=============== 
Log In
===============
*/

.login-form {
    width: 30%;
    min-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.login-form h1 {
    text-align: center;
    text-transform: uppercase;
}

.error-msg {
    display: none;
    background-color: var(--red-light);
    padding: 0.5rem 0.5rem 1rem;
    margin-bottom: 2rem;
}

.login-form input,
.login-form button {
    border-radius: 0;
    outline: none;
    border: none;
    padding: 10px;
}

.login-form input {
    margin-bottom: 1rem;
    background-color: var(--primary-100);
}

.login-form button {
    margin-top: 2rem;
    cursor: pointer;
    background-color: var(--primary-500);
    color: var(--black);
    transition: var(--transition);
    opacity: 0.8;
}
.login-form button:hover {
    opacity: 1;
}

@media screen and (max-width: 850px) {
    .login-form {
        width: 100%;
        min-width: unset;
    }
}

